import Vue from "vue";
import App from "./App.vue";
import VueRouter from "vue-router";
Vue.use(VueRouter);

Vue.config.productionTip = false;
// 引入iveiw框架
import "./lib/iview";
// 引入elementui框架
import "./lib/elementui";
//引入app.vue的基础样式
import "./assets/less/basic.css";

import index from "./components/index.vue";
//给路有添加引导
let routes = [
  {
    path: "/index",
    compontent: index
  }
];
// 实例化路有
let router = new VueRouter({
  routes
});
new Vue({
  render: h => h(App),
  router
}).$mount("#app");
